import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'formulario-dados',   templateUrl: 'formulario_dados.component.html' }) export class FormularioDadosComponent implements OnInit {       displayMode: FormularioDadosDisplayModeEnum;     displayModeEnum = FormularioDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = FormularioDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = FormularioDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = FormularioDadosDisplayModeEnum.Edit;           break;       }     }  }  enum FormularioDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


